import React, { PureComponent, Fragment, createRef } from 'react';
import { createPortal } from 'react-dom'
import { PDFDocument } from 'pdf-lib'

import Box from 'components/Box';
import Flex from 'components/Flex';
import Container from 'components/Container';
import Circle from 'components/Circle';
import Download from 'components/Download';
import Button from 'components/Button';
import A4Page from 'components/A4Page';
import withResponsive from 'hoc/withResponsive'

import { mobileOrDesktop } from 'components/ThemeProvider/theme';

import saveAs from 'vendor/FileSaver'

import DateCard from './MemoPage/DateCard'
import MemoResult from './MemoPage/MemoResult'

let html2canvas
if (process.env.NODE_ENV === 'development') {
  html2canvas = require('html2canvas')
  require('whatwg-fetch')
}

const a4Landscape = {
  height: 595.28,
  width: 841.89,
}

const DownloadModule = ({ text, onClick, disabled, ...props }) => (
  <Flex
    width={mobileOrDesktop(1, 1/2)}
    alignItems="center"
    py={mobileOrDesktop('0.5em', 0)}
    {...props}
    color="lightOrange"
  >
    <Box mr="1em" fontWeight="bold" color="text">{text}</Box>
    <Box width="2.5em">
      <Circle onClick={onClick} disabled={disabled} is={Button.download}>
        <Download width="1.25em" />
      </Circle>
    </Box>
  </Flex>
)

class Downloader extends PureComponent {
  state = {
    imgLoading: false,
    pdfLoading: false,
  }

  componentDidMount() {
    html2canvas = require('html2canvas')
    require('whatwg-fetch')
    require('vendor/canvas-toBlob')
    this.takeWith = `${window.location.origin}/takeWith.pdf`
  }

  screenDom = createRef()
  a4Dom = createRef()

  setImgLoading = imgLoading => this.setState({ imgLoading })
  setPdfLoading = pdfLoading => this.setState({ pdfLoading })

  setLoading = key => {
    this.saveScroll()
    this.setState({ [`${key}Loading`]: true })
  }

  unsetLoading = key => {
    this.restoreScroll()
    this.setState({ [`${key}Loading`]: false })
  }

  saveScroll = () => {
    this.lastScrollY = window.scrollY
    window.scrollTo(0, 0)
  }

  restoreScroll = () => {
    window.scrollTo(0, this.lastScrollY)
  }

  render() {
    const { memo, isMobile, noticeList } = this.props
    const { imgLoading, pdfLoading } = this.state;

    return (
      <Fragment>
        <Flex mt="1em" flexWrap="wrap">
          <DownloadModule
            text={`下載為適合${isMobile ? '手機螢幕' : '直接分享'}的圖片`}
            pr={mobileOrDesktop(0, '2em')}
            justifyContent={mobileOrDesktop('space-between', 'flex-end')}
            borderRight={mobileOrDesktop('none', '1px solid')}
            disabled={imgLoading}
            onClick={() => {
              this.setLoading('img')
              html2canvas(this.screenDom.current).then((canvas) => {
                canvas.toBlob((blob) => {
                  this.unsetLoading('img')
                  saveAs(blob, 'doctor-memo.png');
                })
              });
            }}
          />
          <DownloadModule
            text="下載為可列印的PDF檔案"
            pl={mobileOrDesktop(0, '2em')}
            justifyContent={mobileOrDesktop('space-between', 'flex-start')}
            borderLeft={mobileOrDesktop('none', '1px solid')}
            borderTop={mobileOrDesktop('1px solid', 'none')}
            disabled={pdfLoading}
            onClick={() => {
              this.setLoading('pdf')
              if (memo) {
                // const doc = new jsPDF({
                //   orientation: 'landscape',
                // });
                Promise.all([
                  PDFDocument.create(),
                  window.fetch(this.takeWith)
                    .then(res => res.arrayBuffer())
                    .then((bytes) => PDFDocument.load(bytes)),
                  html2canvas(this.a4Dom.current),
                ]).then(([
                  pdfDoc,
                  takePdf,
                  canvas,
                ]) => {
                  const page = pdfDoc.addPage([a4Landscape.width, a4Landscape.height])
                  const ratio = canvas.height / canvas.width;
                  const height = a4Landscape.width * ratio
                  Promise.all([
                    pdfDoc.copyPages(takePdf, [0]),
                    pdfDoc.embedPng(canvas.toDataURL()),
                  ]).then(([
                    [existingPage],
                    img,
                  ]) => {
                    page.drawImage(img, {
                      x: 0,
                      y: (a4Landscape.height - height) / 2,
                      width: a4Landscape.width,
                      height,
                    })
                    if (noticeList) {
                      pdfDoc.insertPage(1, existingPage)
                    }
                    pdfDoc.save().then((arrayBuffer) => {
                      saveAs(new Blob([arrayBuffer]), 'doctor-memo.pdf');
                      this.unsetLoading('pdf')
                    })
                  })
                })
              } else {
                window.open(this.takeWith)
                this.unsetLoading('pdf')
              }
            }}
          />
        </Flex>
        {createPortal((imgLoading || pdfLoading) && (
          <Box position="fixed" top="0" left="0" right="0" bottom="0" bg="rgba(0,0,0,0.8)" zIndex={9999}>
            <Box position="absolute" top="50%" left="50%" transform="translate(-50%, -50%)" color="white" fontSize="1.5em">
              檔案生成中...
            </Box>
          </Box>
        ), document.getElementById('___gatsby'))}
        {createPortal((
          <Box position="absolute" top="-9999px" left="-999px" opacity="0">
            <Box ref={this.screenDom} bg="lightBgOrange">
              <Container py="2em">
                {memo && (
                  <DateCard>
                    <MemoResult memo={memo} />
                  </DateCard>
                )}
                {noticeList || null}
              </Container>
            </Box>
          </Box>
        ), document.getElementById('___gatsby'))}
        {createPortal((
          <Box position="absolute" top="-9999px" left="-999px" opacity="0">
            <Box ref={this.a4Dom}>
              <A4Page px="2cm" py="1cm">
                {memo && <MemoResult memo={memo} forceDesktop />}
              </A4Page>
            </Box>
          </Box>
        ), document.getElementById('___gatsby'))}
      </Fragment>

    );
  }
}

export default withResponsive(Downloader);
