import React, { useState } from 'react';
import Measure from 'react-measure'

import Box from 'components/Box';
import Circle from 'components/Circle';

import Body from './Body'

const BodyPoints = ({ onNewDot, onDotClick, dots, ...props }) => {
  const [dims, setBounds] = useState({})
  const handleImageClick = e => {
    onNewDot([(e.clientX - dims.left) / dims.width * 100, (e.clientY - dims.top) / dims.height * 100])
  }
  const handleDotClick = (i) => () => onDotClick(i);
  console.log(dims)
  return (
    <Box position="relative" {...props}>
      <Measure bounds onResize={({ bounds }) => setBounds(bounds)}>
        {({ measureRef }) => (
          <Box pt="100%" position="relative" ref={measureRef}>
            <Box
              position="absolute"
              top="0"
              left="0"
              right="0"
              bottom="0"
              onClick={onNewDot && handleImageClick}
            >
              <Body width={dims.width} />
            </Box>
          </Box>
        )}
      </Measure>
      {Boolean(dots.length) && dots.map((dot, i) => (
        <Box
          key={i}
          top={`${dot.position[1]}%`}
          left={`${dot.position[0]}%`}
          position="absolute"
          transform="translate(-50%, -50%)"
          onClick={onDotClick && handleDotClick(i)}
        >
          <Circle
            width="1.75rem"
            bg="orange"
            fontSize={i > 8 ? '1.25em' : '1.5em'}
            left={i > 8 && '48%'}
            color="white"
            fontWeight="bold"
          >
            {i + 1}
          </Circle>
        </Box>
      ))}
    </Box>
  );
};

export default BodyPoints;
